.repos {
  width: 900px;
  padding-left: calc(100vw - 100%);
  margin: 0 auto; }
  .repos h2 {
    text-transform: capitalize; }
  @media only screen and (max-width: 950px) {
    .repos {
      width: 600px; } }
  @media only screen and (max-width: 620px) {
    .repos {
      width: 300px; } }



